<template>
<div class="container">
   <div class="number-container-field mb-5">
      <p>Артикль:</p>
      <span>
         {{ numberFields }}
      </span>

   </div>
   <button @click="clearNumber()" class="clear-button" color="blue">СТЕРЕТЬ</button>
   <div class="number-flex">
      <div class="number-row" v-for="number in numbersArr" :key="number">
         <button class="numbers-btn" @click="addNumber(number)">{{ number }}</button>
      </div>
   </div>

   <button :disabled="numberFields.length <= 7" :style="{'opacity': numberFields.length <= 7 ? 0.5 : 1}" class="button" @click="startDevelop()">Начать наработку</button>

</div>
</template>

<script>
export default {
   data: () => ({
      buttonDiabled: true,
      numbersArr: [
         1, 2, 3, 4, 5, 6, 7, 8, 9, 0
      ],
      numberFields: ' ',
      saveNumber: ''
   }),
   methods: {
      addNumber(number) {

         if (this.numberFields.length <= 7) {
            this.numberFields += number
         }

      },
      clearNumber() {
         this.numberFields = this.numberFields.slice(0, -1)
      },
      startDevelop() {
         this.$router.push(`/DevelopmentInterface/${this.numberFields}`);
      }
   },
   computed: {
      isButtonDiabled() {
         let button = this.buttonDiabled
         if (this.numberFields.length > 6) {
            button = false
         }
         return button
      }
   }
}
</script>

<style>
.container {
   max-width: 365px;
   margin: 0 auto;
   width: 100%;
   height: 100%;
}

.number-container-field {
   max-width: 340px;
   margin: 0 auto;
   height: 140px;
   background-color: rgb(255, 255, 255);
   font-size: 42px;
   font-weight: 600;
   padding: 10px;
   position: relative;
}

.clear-button {

   background-color: rgb(229, 30, 30);
   color: #fff;
   justify-content: center;
   width: 100%;
   margin-bottom: 20px;
   padding: 2px 0;
   font-size: 42px;

}

.number-container-field p {
   font-size: 32px;
}

.number-flex {
   margin: 0 auto;
   display: flex;
   justify-content: center;
   flex-wrap: wrap;
   column-gap: 2px;
   row-gap: 5px;

}

.numbers-btn {
   background-color: #1E88E5;
   color: #fff;
   font-size: 44px;
   width: 110px;
}

.button {
   background-color: #309511;
   color: #fff;
   justify-content: center;
   width: 290px;
   padding: 10px 0;
   font-size: 32px;
   display: flex;
   margin: 15px auto 0;
}
</style>
